<template>
  <div class="game-main">
    <div class="game-info">
      <ul class="clear-fix">
        <li>
          <div class="logo">
            <a :class="{ 'no-bg': !isShowColorInLogo }" href="javascript:;">
              <el-image
                :src="gameDetail.logo"
                fit="scale-down"
                class="betting-lottery-logo"
              >
                <div slot="error" class="error-image-slot"></div>
              </el-image>
            </a>
          </div>
        </li>
        <li>
          <div class="lottery-time">
            <div class="left">
              <div class="white-bg">
                {{ $t("gameInfo.the") }}
                <i class="text_color">{{ sscAwardInfo["issue"] }}</i>
                {{ $t("gameInfo.stage") }}
              </div>
              <div v-if="openPromptVoice !== -1">
                <p class="white-bg">{{ $t("gameInfo.lotteryTime") }}</p>
                <span
                  class="voice text_color"
                  @click="setVolumeWinFlagFn('volum')"
                  ><i v-if="openPromptVoice" class="icon-volume-medi"></i
                  ><i v-else class="icon-volume-mute2"></i
                ></span>
              </div>
            </div>
            <div class="time">
              <span class="hour">{{ endTimeInfo.hour }}</span>
              <i>:</i>
              <span class="minute">{{ endTimeInfo.min }}</span>
              <i>:</i>
              <span class="second">{{ endTimeInfo.sec }}</span>
            </div>
          </div>
        </li>
        <li>
          <div class="dice-group-box lottery-number">
            <div class="left">
              <div class="white-bg">
                {{ $t("gameInfo.the") }}
                <i class="text_color">{{ sscLastWnNumber["issue"] }}</i>
                {{ $t("gameInfo.stage") }}
              </div>
              <div v-if="openPromptVoice !== -1">
                <p class="white-bg">{{ $t("gameInfo.lotteryNumber") }}</p>
                <el-switch v-model="winningReminder" inactive-color="#dcdfe6">
                </el-switch>
              </div>
            </div>
            <div class="dice-group ball-list">
              <span
                v-for="(item, index) in wnNumberList"
                :key="index"
                :class="{ ['cell' + +item]: true }"
                class="dice-box border_color"
                v-html="cellHtml(item)"
              ></span>
            </div>
          </div>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
import { mapGetters } from "vuex";
import { lotteryMixin } from "../../../common/js/mixin";
import { getAjaxMixins } from "../../../common/js/getAjaxMixins";
import { cellHtml } from "../../../util/index";

export default {
  mixins: [lotteryMixin, getAjaxMixins],
  data() {
    return {};
  },
  methods: {
    cellHtml(number) {
      return cellHtml(number);
    }
  },
  computed: {
    ...mapGetters({
      menuList: "menu"
    }),
    ...mapGetters([
      "isChoiceShow",
      "szcpMenuList",
      "lotteryId",
      "sscAwardInfo",
      "sscLastWnNumber",
      "userInfo",
      "gameDetail"
    ]),
    wnNumberList() {
      const wnNumber = this.sscLastWnNumber["wn_number"];
      return wnNumber ? wnNumber.split("") : ["?", "?", "?"];
    }
  },
  created() {}
};
</script>

<style scoped>
@import "../statics/css/bettingTray.css";
</style>
<style lang="less">
.dice-group-box {
  .dice-group {
    height: 75px;
    display: flex;
    display: -ms-flexbox;
    justify-content: center;
    align-items: center;
    -ms-flex-pack: center;
    -ms-flex-align: center;
    -ms-flex-wrap: wrap;
    flex-wrap: wrap;
    overflow: hidden;
    .dice-box {
      border-radius: 8px;
      display: flex;
      display: -ms-flexbox;
      padding: 6px 8px;
      width: 53px;
      height: 53px;
      text-align: center;
      font-size: 32px;
      background-image: linear-gradient(0deg, #eaa84b 0%, #ffffb9 100%),
        linear-gradient(#ffffff, #ffffff);
      background-blend-mode: normal, normal;
      box-shadow: 0px 6px 7px 0px rgba(0, 0, 0, 0.3);
      border: solid 2px #9f6900;
      line-height: 50px;
      margin: 0 8px;
      transition: all 0.5s;
      &:hover {
        background-image: linear-gradient(0deg, #eaa84b 0%, #ffffb9 100%),
          linear-gradient(#ffffff, #ffffff);
      }
      i {
        display: block;
        height: 10px;
        width: 10px;
        border-radius: 50%;
        box-shadow: 0 1px 1px 1px rgba(255, 255, 255, 0.5),
          inset 0 1px 3px 0 rgba(0, 0, 0, 0.5);
      }
      &.cell1 {
        justify-content: center;
        align-items: center;
        i {
          height: 15px;
          width: 15px;
          box-shadow: 0 1px 1px 1px rgba(255, 255, 255, 0.5),
            inset 0 1px 3px 0 rgba(0, 0, 0, 0.5);
        }
      }

      &.cell2 {
        justify-content: space-around;
        flex-direction: column;
        align-items: center;
      }
      &.cell3 {
        justify-content: space-between;
        flex-direction: column-reverse;
        i {
          &:nth-child(2) {
            align-self: center;
          }
          &:nth-child(3) {
            align-self: flex-end;
          }
        }
      }
      &.cell4 {
        flex-direction: column;
        justify-content: space-between;
        div {
          display: flex;
          flex-direction: row;
          justify-content: space-between;
          display: -ms-flexbox;
          -ms-flex-direction: row;
          -ms-flex-pack: justify;
        }
      }
      &.cell5 {
        flex-direction: column;
        justify-content: space-between;
        div {
          display: flex;
          flex-direction: row;
          justify-content: space-between;
          display: -ms-flexbox;
          -ms-flex-direction: row;
          -ms-flex-pack: justify;

          &:nth-child(2) {
            justify-content: center;
          }
        }
      }
      &.cell6 {
        flex-direction: column;
        flex-wrap: wrap;
        align-content: space-between;
        justify-content: space-between;
      }
      &.cellNaN {
        i {
          display: none;
        }
        &:after {
          content: "?";
          display: block;
          width: 100%;
          height: 100%;
          text-align: center;
          line-height: 37px;
        }
      }
    }
  }
}
</style>

